.notification-wrapper {
    font-size: 14px;
    padding: 15px;
    padding-bottom: 0;
    border-bottom: 1px solid #dfe3e6;
}

.notification-wrapper > h4 {
    margin: 0;
}

.notification-wrapper > p {
    margin: 15px 0;
}

.notification-wrapper > hr {
    margin: 0;
}

.promo-img {
    width: 150px;
    height: 150px;
    margin: 15px 0;
}

.loading {
    background: transparent url('../../images/spinner.svg') center no-repeat;
}

.cursor-pointer {
    cursor: pointer;
}

.notification-wrapper:hover {
    background-color: #dfe3e6;
}